jQuery(window).on("resize.topmenu", function() {
  if ($notif.length) {
    var top=Math.max(0, $notif.innerHeight()-jQuery(window).scrollTop());
    if (top)
      jQuery("header").removeClass("fixed");
    else
      jQuery("header").addClass("fixed");
  }
}).trigger("resize.topmenu").on("scroll.topmenu", function() {
  jQuery(window).trigger("resize.topmenu");
}).on("load.topmenu", function() {
  jQuery(window).trigger("resize.topmenu");
});

jQuery(function($) {
  $("#notification .close").click(function(e) {
    e.preventDefault();
    $("#notification").addClass("closed");
    jQuery(window).trigger("resize.topmenu");
  });
});

jQuery(function($) {
  $("body > header nav > ul > li > ul > li.has-child, body > header .my-egov>ul>li.has-child").mouseenter(function(e) {
    if ($(window).innerWidth()>990) {
      var ul=$(this).find(">ul");
      var ulTop=ul.css("top").replace("px", "")*1;
      var offsetBottom=ul.offset().top+ul.innerHeight();
      var winBottom=$(window).innerHeight()+$(window).scrollTop();
      var ulTop=Math.min(-10, winBottom-offsetBottom+ulTop);
      ul.css("top", ulTop);
    } else {
      ul.css("top", "");
    }
  });
});

(function($) {
  var myEgovWidth=0;
  var mainNavWidth=0;
jQuery(window).on("resize.myegov", function() {
  var windowWidth=$("header>.container").innerWidth();
  if (windowWidth-myEgovWidth-mainNavWidth<100)
    $("header .nav, header .my-egov").addClass("narrow");
  else
    $("header .nav, header .my-egov").removeClass("narrow");
}).on("load.myegov", function() {
  var myEgov=$("header .my-egov");
  var mainNav=$("header .nav>nav");
  myEgov.find(".my-data").attr("title", (myEgov.find(".my-data").html() || "").replace(/\<[^>]*\>/g, "").trim());
  myEgov.find(".my-activity").attr("title", (myEgov.find(".my-activity").html() || "").replace(/\<[^>]*\>/g, "").trim());
  mainNavWidth=mainNav.innerWidth();
  myEgovWidth=myEgov.innerWidth();
  $(window).trigger("resize.myegov");
});
})(jQuery);

