var beforeUnloadSkip=false;
jQuery(window).on("beforeunload", function(e) {
  if (beforeUnloadSkip)
    return beforeunloadSkip=false;
  var loadingBox=jQuery("<div />", {"class": "loading"});
  jQuery("body").append(loadingBox);
});

function showLoadingBar() {
  jQuery("body").append(jQuery("<div />", {"class": "loading"}));
}
function hideLoadingBar() {
  jQuery("body>.loading").remove();
}

window.showLoadingBar=showLoadingBar;
window.hideLoadingBar=hideLoadingBar;
